import Autoplay from 'embla-carousel-autoplay'

import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from '@/components/ui/carousel'
import Image from './image'
import { cn } from '@/lib/utils'

export function CarouselDemo({
  posts,
  isHome = false,
}: {
  posts: Ad[]
  isHome?: boolean
}) {
  return (
    <Carousel
      // onMouseEnter={autoplay.stop}
      // onMouseLeave={autoplay.reset}
      opts={{ loop: true }}
      className="max-w-screen"
      plugins={[
        Autoplay({
          delay: 6000,
        }),
      ]}
    >
      <CarouselContent>
        {posts.map((post, index) => (
          <CarouselItem key={index}>
            <div
              className={cn(
                'rounded-2xl  flex aspect-16/9 items-center justify-center my-2.5 mx-1.5 shadow-md  shadow-black/30',
                isHome && 'rounded-xl ',
              )}
            >
              <a href={post.link} target="_blank" rel="noopener noreferrer">
                <Image
                  src={{
                    src: `https://images2.fibladi.com/H500/${post.image.split('.')[0]}.webp`,
                  }}
                  altReal={post.titre}
                  className={cn(
                    'w-full border border-neutral-300 aspect-16/9  rounded-2xl object-cover brightness-100 transition-[filter] duration-300 group-hover:brightness-75',
                    isHome && 'rounded-xl',
                  )}
                  fill
                />
              </a>
            </div>
          </CarouselItem>
        ))}
      </CarouselContent>
      <CarouselPrevious />
      <CarouselNext />
    </Carousel>
  )
}

export function CarouselDemoDesktop({ posts }: { posts: Ad[] }) {
  return (
    <Carousel
      plugins={[
        Autoplay({
          delay: 6000,
        }),
      ]}
      opts={{ loop: true }}
      className="max-w-screen px-8"
    >
      <CarouselContent>
        {[...posts, ...posts].map((post, index) => (
          <CarouselItem key={index} className="md:basis-1/2 lg:basis-1/3">
            <div className="flex aspect-16/9  rounded-2xl items-center justify-center  m-2.5 mt-0 shadow-md  shadow-black/30">
              <a
                className=""
                href={post.link}
                target="_blank"
                rel="noopener noreferrer"
              >
                <Image
                  src={{
                    src: `https://images2.fibladi.com/H500/${post.image.split('.')[0]}.webp`,
                  }}
                  altReal={post.titre}
                  className="w-full border-neutral-500 border-y-1 aspect-16/9 rounded-2xl  object-cover brightness-100 transition-[filter] duration-300 group-hover:brightness-75"
                  fill
                />
              </a>
            </div>
          </CarouselItem>
        ))}
      </CarouselContent>
      <CarouselPrevious className="-left-1" />
      <CarouselNext className="-right-1" />
    </Carousel>
  )
}

export interface TopAdsResponse {
  data: Ad[]
  links: PaginationLinks
  meta: PaginationMeta
}

export interface Ad {
  id: string
  date: string // e.g. "2024-04-21 12:51:35"
  region: string | null
  titre: string
  texte: string
  code: string
  prix: number
  'prix-old': number | null
  categorieancetre: string | null
  nomcommercial: string | null
  phone: string | null
  image: string
  video: string
  link: string
}

export interface PaginationLinks {
  first: string
  last: string
  prev: string | null
  next: string | null
}

export interface PaginationMeta {
  current_page: number
  from: number
  last_page: number
  links: PageLink[]
  path: string
  per_page: number
  to: number
  total: number
}

export interface PageLink {
  url: string | null
  label: string
  active: boolean
}
